.container {
    background-color: white;
    border-radius: 10px;
    z-index: 999;
    position: fixed;
    top: 35%;
    left: 30%;
    box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.75);
}
.wrapper {
    margin: 20px;
    padding: 20px;
}
.h5 {
    font-weight: 600;
    font-size: 20px;
}
.buttonsWrapper {
    display: flex;
    justify-content: space-around;
}
.button {
    background-color: #fc5321;
    color: rgb(255, 255, 255);
    border: none;
    border-radius: 10px;
    padding: 10px;
    width: 100px;
    text-transform: uppercase;
    outline: none;
}
.button:hover {
    cursor: pointer;
    box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.75);
}

@media screen and (max-width: 1280px) {
    .container {
        left: 30%;
    }
}
@media screen and (max-width: 1010px) {
    .container {
        left: 25%;
    }
    .wrapper {
        margin: 10px;
        padding: 10px;
    }
    .h5 {
        font-size: 18px;
    }
}
@media screen and (max-width: 750px) {
    .container {
        left: 25%;
    }
    .wrapper {
        margin: 10px;
        padding: 10px;
    }
    .h5 {
        font-size: 16px;
    }
}
@media screen and (max-width: 650px) {
    .container {
        left: 22%;
    }
    .h5 {
        font-size: 14px;
    }
    .button {
        max-width: 80px;
    }
}
@media screen and (max-width: 533px) {
    .container {
        left: 20%;
    }
    .wrapper {
        margin: 5px;
        padding: 5px;
    }
    .h5 {
        font-size: 12px;
    }
    .button {
        max-width: 60px;
        font-size: 10px;
    }
}
@media screen and (max-width: 400px) {
    .container {
        left: 15%;
    }
    .wrapper {
        margin: 5px;
        padding: 5px;
    }
    .h5 {
        font-size: 10px;
    }
    .button {
        max-width: 50px;
        font-size: 8px;
        padding: 5px;
    }
}
