.container {
    position: fixed;
    top: 25%;
    width: 100%;
}

.wrapper {
    width: 70%;
    display: flex;
    flex-wrap: wrap;
    margin: 10px auto;
    align-items: center;
    justify-content: space-evenly;
    position: relative;
    background-color: rgb(250, 250, 250, 0.4);
    padding: 20px 0px;
    border-radius: 10px;
    height: 500px;
    overflow-y: scroll;
    scrollbar-width: none;
}
.wrapper::-webkit-scrollbar {
    display: none;
}

.imgWrapper {
    height: 350px;
    width: 300px;
    /* position: relative; */
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    padding-bottom: 40px;
}
.img {
    object-fit: cover;
    height: 100%;
    width: 100%;
}
.img:hover {
    cursor: pointer;
    box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.75);
    -webkit-box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.75);
}
