.wrapper {
    position: fixed;
    top: 30%;
    width: 100%;
}
.between {
    border-top: 1px solid white;
    border-bottom: 1px solid white;
    padding: 7px 0;
    width: 60%;
    margin: 0 auto;
}
.titleWrapper {
    border-top: 2px solid white;
    border-bottom: 2px solid white;
    padding: 5px 20px;
}

.name {
    font-family: "Dancing Script", cursive;
    font-size: 120px;
    padding: 0;
    margin: 0;
    font-weight: 400;
}
.prof {
    font-size: 30px;
    padding: 0;
    margin: 0;
    text-transform: uppercase;
    font-weight: 500;
}

.gallery {
    background-color: #fc5321;
    color: rgb(255, 255, 255);
    outline: none;
    border: none;
    padding: 10px;
    font-size: 20px;
    border-radius: 10px;
    text-transform: capitalize;
    font-style: italic;
    margin-top: 100px;
    text-decoration: none;
}
.gallery:hover {
    cursor: pointer;
    box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.75);
    font-size: 21px;
}

.ripple {
    display: inline-block;
    position: relative;
    width: 20px;
    height: 20px;
    margin-left: 5px;
}
.div {
    position: absolute;
    border: 4px solid #ffff;
    opacity: 0;
    border-radius: 50%;
    animation: ripple 01s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.div:nth-child(2) {
    animation-delay: -0.5s;
}
@keyframes ripple {
    0% {
        top: 10px;
        left: 10px;
        width: 0;
        height: 0;
        opacity: 1;
    }
    100% {
        top: 0px;
        left: 0px;
        width: 20px;
        height: 20px;
        opacity: 0;
    }
}

@media screen and (max-width: 1300px) {
    .between {
        width: 80%;
    }
}
@media screen and (max-width: 1180px) {
    .between {
        width: 80%;
    }
}
@media screen and (max-width: 984px) {
    .name {
        font-size: 80px;
    }
}
@media screen and (max-width: 655px) {
    .wrapper {
        top: 40%;
    }
    .name {
        font-size: 64px;
    }
    .prof {
        font-size: 20px;
    }
    .gallery {
        display: none;
    }
}
@media screen and (max-width: 550px) {
    .between {
        width: 90%;
    }
    .name {
        font-size: 54px;
    }
    .prof {
        font-size: 25px;
        padding-top: 10px;
    }
}
@media screen and (max-width: 420px) {
    .between {
        width: 90%;
    }
    .name {
        font-size: 42px;
    }
    .prof {
        font-size: 20px;
        padding-top: 10px;
    }
}
